ng","srcset":"https://sika.scene7.com/is/image/sika/glo-annual-report-target-market-sealing-bonding?fit=crop%2C1&wid=600&hei=360&fmt=png-alpha" + " 1x, " + "https://sika.scene7.com/is/image/sika/glo-annual-report-target-market-sealing-bonding?fit=crop%2C1&wid=960&hei=577&fmt=png-alpha" + " 1.5x"}}),_c('img',{staticClass:"cmp-image__image",attrs:{"src":"https://sika.scene7.com/is/image/sika/glo-annual-report-target-market-sealing-bonding?fit=crop%2C1&wid=600&hei=360&fmt=","alt":"Target Market Sealing & Bonding"}})])])])])]),_c('div',{staticClass:"cell small-12 medium-6 large-6"},[_c('div',{staticClass:"textimage section"},[_c('div',{staticClass:"cmp-textimage cmp-text cmp-textimage__list-bullet"},[_c('h2',[_vm._v("Клеи и герметики")]),_c('p',[_vm._v("Компания Sika предлагает широкий ассортимент высокоэффективных и долговечных герметиков,  герметизирующих лент, монтажных пен и эластичных клеев для наружной оболочки зданий, для внутренней отделки и для строительства объектов инфраструктуры. Наиболее распространёнными сферами применения продукции Sika являются герметизация подвижных деформационных швов между элементами фасадов для гидроизоляции зданий, приклеивание деревянных напольных покрытий для снижения шума или герметизация соединений на перронах в аэропортах. Растущий спрос на этом рынке обусловлен пониманием важности высококачественных герметиков и клеев для обеспечения общей долговечности, износостойкости и энергоэффективности зданий, рост урбанизации, включая строительство многоэтажных высотных домов, и продолжающейся заменой механических крепёжных систем на клеи по причине их более высокого качества и эффективности."),_c('br')])])])])])])])]),_c('div',{staticClass:"layoutcontainer section"},[_c('div',[_c('div',{staticClass:"cmp-layout grid-container"},[_c('div',{staticClass:"cmp-layout__content grid-x grid-margin-x"},[_c('div',{staticClass:"cell small-12 medium-6 large-6"},[_c('div',{staticClass:"textimage section"},[_c('div',{staticClass:"cmp-textimage cmp-text cmp-textimage__list-bullet"},[_c('h2',[_vm._v("Ремонт и защита бетона")]),_c('p',[_vm._v("В этом сегменте представлены решения для защиты и ремонта бетонных конструкций, такие как, например, ремонтные растворы, защитные покрытия, смеси и элементы для усиления конструкций. Ассортимент также включает продукцию для внутренней отделки, такую как выравнивающие смеси, плиточные клеи, плиточные затирки, а также фасадные штукатурки для наружного использования. Компания Sika обеспечивает технологии для всего жизненного цикла коммерческих зданий, жилых домов и объектов инфраструктуры. Множество зданий и сооружений, особенно на развитых рынках, являются старыми и требуют ремонта. Наблюдаемый в настоящее время рост спроса обусловлен увеличением количества проектов по ремонту объектов инфраструктуры в транспортной отрасли, в сфере управления водными ресурсами и в энергетическом секторе. Глобальная тенденция урбанизации и увеличивающиеся потребности в реновации на развивающихся рынках также способствуют увеличению спроса."),_c('br')])])])]),_c('div',{staticClass:"cell small-12 medium-6 large-6"},[_c('div',{staticClass:"textimage section"},[_c('div',{staticClass:"cmp-textimage cmp-textimage__list-bullet"},[_c('div',{staticClass:"cmp-image-- cmp-image"},[_c('picture',{attrs:{"data-image-src":"{\"isResponsive\":true,\"pattern\":\"https://sika.scene7.com/is/image/sika/glo-annual-report-target-market-refurbishment?fit\\u003dcrop%2C1\\u0026wid\\u003d${width}\\u0026fmt\\u003d${format}\\u0026cropN\\u003d0.00546875%2C0.26270373921380635%2C0.984375%2C0.6807286673058485\",\"originalWidth\":2744,\"originalHeight\":2234,\"alt\":\"refurbishment target market\",\"imageProfileNamePrefix\":\":\",\"imageProfileMap\":{}}","data-height-based":"false"}},[_c('source',{attrs:{"type":"image/webp","srcset":"https://sika.scene7.com/is/image/sika/glo-annual-report-target-market-refurbishment?fit=crop%2C1&wid=600&fmt=webp-alpha&cropN=0.00546875%2C0.26270373921380635%2C0.984375%2C0.6807286673058485" + " 1