звукоизоляции, ассортимент компании Sika также включает продукцию для внутренних и внешних стен, такую как материалы для выравнивания стен, декоративную отделочную штукатурку и наружные изоляционные и отделочные системы для фасадов (EIFS). Глобальная тенденция урбанизации и растущая потребность в ремонте и обустройстве домашнего жилья придаёт дополнительный импульс развитию этого рынка. Благодаря своим твёрдым позициям среди быстро развивающихся каналов поставок, обслуживанию широкого спектра самых разнообразных подрядчиков и наличию комплексного портфеля дополнительных вспомогательных технологий для каркаса и внешней оболочки зданий, компания Sika успешно удовлетворяет растущий спрос на высококачественные, комфортные, эстетические и экологически чистые решения."),_c('br')])])])])])])])]),_c('div',{staticClass:"layoutcontainer section"},[_c('div',[_c('div',{staticClass:"cmp-layout grid-container"},[_c('div',{staticClass:"cmp-layout__content grid-x grid-margin-x"},[_c('div',{staticClass:"cell small-12 medium-6 large-6"}),_c('div',{staticClass:"cell small-12 medium-6 large-6"})])])])])])
